
























































import Vue from 'vue';
import {Component} from 'vue-property-decorator';

@Component
export default class History extends Vue {
  cur = 0;
  // eslint-disable-next-line @typescript-eslint/explicit-module-boundary-types
  changeCur() {
    if( this.cur === 0) {
      this.cur = 1;
    } else {
      this.cur = 0;
    }
  }
}
